import React from "react";
import { useAddStudent } from "../../../Context/AddStudent";
import useAxios from "../../../api/Axios";
import { MdClose } from "react-icons/md";
import styled from "styled-components";
import AddNew from "./AddNewForm";

const ViewStudentOtherFromImport = () => {
  const axios = useAxios();
  const students = useAddStudent();
  const [loading, setLoading] = React.useState(true);
  // const [array, setArray] = React.useState([]);
  React.useEffect(() => {
    setLoading(true);
    const getStudents = async () => {
      const { year, semester, courseName, term, center } = students.otherInfo;
      const url = `/student/get?year=${year}&semester=${semester}&term=${term}&courseName=${courseName}&center=${center}`;
      await axios
        .get(url)
        .then((res) => {
          handleData(res.data);
          setLoading(false);
        })
        .catch((err) => {
          students.handleError(err?.response?.data?.message);
          setLoading(false);
        });
    };

    if (students.array.length === 0) {
      getStudents();
    }
    setLoading(false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [axios]);

  const handleData = (data) => {
    const newData = data.map((dt) => {
      const { stuId, stuName } = dt;
      const { year, semester, term, classId, courseName } = students.classInfo;
      return [
        Number(stuId),
        `${stuName}`,
        classId,
        year,
        semester,
        `${courseName}`,
        term,
        new Date().toUTCString(),
      ];
    });
    students.setArray(newData);
  };

  return (
    <Panel>
      <Title>View Student Other From Import</Title>

      {loading ? (
        <ImageContainer style={{ justifyContent: "center" }}>
          <Loader>Loading...</Loader>
        </ImageContainer>
      ) : (
        <React.Fragment>
          {students.array.length > 0 ? (
            <React.Fragment>
              <FlexRow>
                <InputGroup>
                  <Label>Student ID:</Label>
                </InputGroup>
                <InputGroup>
                  <Label>Student Name:</Label>
                </InputGroup>
              </FlexRow>
              {students.array.map((dt, idx) => {
                return (
                  <FlexRow key={idx}>
                    <InputGroup>
                      <Input
                        className="stuId"
                        type="Number"
                        defaultValue={dt[0]}
                        placeholder="18052801001"
                        onChange={(e) => {
                          students.array[idx][0] = e.target.value;
                        }}
                      />
                    </InputGroup>
                    <InputGroup>
                      <Input
                        name="name"
                        type="text"
                        defaultValue={dt[1]}
                        placeholder="Mr. John"
                        onChange={(e) => {
                          students.array[idx][1] = e.target.value;
                        }}
                      />
                    </InputGroup>
                    <ButtonGroup>
                      <IconButton
                        style={{ right: "0", background: "red" }}
                        onClick={() => {
                          setLoading(true);
                          students.handleDecrementArray(
                            idx,
                            students.array,
                            students.setArray
                          );
                          setTimeout(() => {
                            setLoading(false);
                          }, 500);
                        }}
                      >
                        <MdClose />
                      </IconButton>
                    </ButtonGroup>
                  </FlexRow>
                );
              })}
            </React.Fragment>
          ) : (
            <ImageContainer>
              <Image src={require("../../images/empty.png")} alt="Empty" />
              <Text>No Student Found</Text>
              <Text>
                Please Import an xlsx file (You can download a sample file to
                upload student information) where the student information
                already have.
              </Text>
              <Text>or</Text>
              <Text>Input Student Information Manually</Text>
              <MyButton
                style={{
                  background: "green",
                  color: "#fff",
                  borderRadius: "5px",
                }}
                onClick={() => students.handleOpen(true)}
              >
                Import Menu
              </MyButton>
            </ImageContainer>
          )}
          {/**
           * ADD NEW STUDENT IN TABLE
           * FILE DIRECTORY -> components >> assets >> AddStudent >> AddNewForm.jsx
           */}
          <AddNew />
          <ButtonPanel>
            <MyButton
              onClick={() => {
                students.handleOtherImport(true);
              }}
            >
              Back
            </MyButton>

            <MyButton
              onClick={() =>
                students.handleSubmitImportfromOther(students.array)
              }
            >
              Save
            </MyButton>
          </ButtonPanel>
        </React.Fragment>
      )}
    </Panel>
  );
};

export default ViewStudentOtherFromImport;

const Panel = styled.div`
  width: 650px;
  margin-top: 0.5rem;
  padding: 2rem;
  border: 1px solid rgba(3, 3, 3, 0.3);
  display: flex;
  flex-direction: column;

  @media (max-width: 660px) {
    width: 100%;
  }
`;
const Title = styled.h1`
  font-size: 1.2rem;
  font-weight: 600;
  text-align: center;
  margin-bottom: 1.5rem;
`;
const InputGroup = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 0.5rem;
  &:nth-child(1) {
    margin-right: 5px;
  }
`;
const FlexRow = styled.div`
  display: grid;
  grid-template-columns: auto auto 50px;
  overflow: hidden;
  width: 100%;

  @media (max-width: 560px) {
    grid-template-columns: auto;
  }
`;

const ButtonGroup = styled.div`
  display: flex;
  align-items: start;
  justify-content: flex-start;
  padding-top: 5px;

  @media (max-width: 560px) {
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    padding-top: 0;
  }
`;

const Label = styled.label`
  font-size: 0.9rem;
`;

const Input = styled.input`
  padding: 0.4rem 0.5rem;
  border-radius: 3px;
  margin-bottom: 0.3rem;
  border: 1px solid rgba(3, 3, 3, 0.5);

  &::placeholder {
    color: rgba(3, 3, 3, 0.5);
  }
`;
const IconButton = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50x;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  color: #fff;
  font-size: 1.2rem;
  cursor: pointer;
  bottom: 15px;
  &:nth-child(1) {
    margin: 0 5px;
  }
`;

const MyButton = styled.button`
  font-weight: 500;
  background: rgba(230, 230, 230, 0.4);
  margin: 0.5rem 0 0.5rem 0.5rem;
  min-width: 100px;

  &:nth-child(1) {
    background: rgba(255, 71, 87, 1);
    color: #fff;
    border-radius: 3px;
  }
  &:nth-child(2) {
    background: rgba(55, 66, 250, 1);
    color: #fff;
    border-radius: 3px;
  }
  &:hover {
    &:nth-child(1) {
      background: rgba(255, 100, 87, 1);
    }
    &:nth-child(2) {
      background: rgba(55, 100, 250, 1);
    }
  }
`;

const ButtonPanel = styled.div`
  display: flex;
  align-items: end;
  justify-content: flex-end;
  margin-top: 1rem;
  width: 100%;
`;

const Image = styled.img`
  width: 130px;
  opacity: 0.5;

  margin-bottom: 0.5rem;
`;

const ImageContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
`;

const Text = styled.p`
  margin-bottom: 0;
  text-align: center;
  &:nth-child(2) {
    font-size: 1.2rem;
    font-weight: 600;
  }
  &:nth-child(3) {
    font-size: 0.9rem;
    font-weight: 500;
    width: 70%;
  }
  &:nth-child(4) {
    font-size: 0.9rem;
    font-weight: 400;
  }
  &:nth-child(5) {
    font-size: 0.9rem;
    font-weight: 500;
  }
`;

const Loader = styled.div`
  padding: 0.5rem 1rem;
  border-radius: 5px;
  background: #fff;
`;
